import React from 'react';
import {Grid,Paper,Button } from '@material-ui/core';
import Avatar from '@material-ui/core/Avatar';
import LockIcon from '@material-ui/icons/Lock';
import TextField from '@material-ui/core/TextField';
import './Login.css';

function Login(props) {

    
    const avatarStyle={backgroundColor:'#347b9a'}
    const txtStyle={margin:'10px 0px'}
    const btnStyle={margin:'10px 0px'}
    const {email,setEmail,password,setPassword,handleLogin,handleSignup,hasAccount,setHasAccount,emailError,passwordError}=props
    
    return (
        {/*<div className="login-cont">
           <label>Username</label> 
           <input type="text" required value={email} onChange={(e)=> setEmail(e.target.value)}></input>
           
           <p>{emailError}</p>
           <label>Password</label>
           <input type="password" required value={password} onChange={(e)=> setPassword(e.target.value)}></input>
           <p>{passwordError}</p>
           
           <div>
               {hasAccount?(
               <>
               <button onClick={handleLogin}>Login</button>
               <p>Don't have an account ?<span onClick={(e)=> setHasAccount(!hasAccount)}>Sign up</span></p>
               
               </>
               ):(
               <>
               <button onClick={handleSignup}>Sign up</button>
               <p>Already have an account ?<span onClick={(e)=> setHasAccount(!hasAccount)}>Login</span></p>
               </>
               )}
               </div>*/
            },
        <div className="main-container"> 
        <Grid className="grid-container">
            <Paper className="paperStyle" elevation={20}>
                <Grid align="center">
                <Avatar style={avatarStyle}><LockIcon></LockIcon></Avatar>
                    <h1>Sign in</h1>
                </Grid>

                <TextField className="Uname" value={email} onChange={(e)=> setEmail(e.target.value)} style={txtStyle} label="Username" placeholder="Enter Username" fullWidth required />
                <p>{emailError}</p>

                <TextField className="Pwd" value={password} onChange={(e)=> setPassword(e.target.value)} style={txtStyle} type="password" label="Password" placeholder="Enter Password" fullWidth required />
                <p>{passwordError}</p>

                {hasAccount?(
                 <>   
                 <Button variant="contained" onClick={handleLogin} style={btnStyle} color="primary" fullWidth>Log in</Button>
                 <p>Don't have an account ? <strong><span onClick={(e)=> setHasAccount(!hasAccount)}>Sign up</span></strong></p>
                 </>
                ):(
                    <>
                    <Button variant="contained" onClick={handleSignup} style={btnStyle} color="primary" fullWidth>Sign Up</Button>
                    <p>Already have an account ? <strong><span onClick={(e)=> setHasAccount(!hasAccount)}>Login</span></strong></p>
                    </>
                )}
                

                
            </Paper>
        </Grid>
        </div>   
    );
}

export default Login
